.hero {
  background-image: url("/../static/images/hero.jpg");
  background-size: cover;
  background-position: top center;
  width: 100%;
  height: 80vh;
  position: relative; }
  .hero:before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 100%);
    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 100%);
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=0 ); }

.primary.hero-cta {
  position: absolute;
  right: 10%;
  top: 72%; }

.hero-wave {
  position: relative;
  z-index: 5; }
  .hero-wave img {
    width: 100%;
    position: relative;
    top: 91%; }

.main-body {
  position: relative;
  padding: 0 40px;
  z-index: 10; }
  .main-body .img-container {
    border-radius: 4px;
    display: block;
    position: relative; }
    .main-body .img-container img {
      border-radius: 4px;
      margin-bottom: 24px; }
    .main-body .img-container:before {
      content: '';
      position: absolute;
      top: calc(50% - 50% / 2);
      left: -20%;
      width: 150%;
      height: 50%;
      background: rgba(245, 166, 35, 0.3);
      color: red;
      z-index: 0; }



