@import url("https://fonts.googleapis.com/css?family=Niconne|Roboto:400,700");
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0;
  width: 100vw;
  overflow-x: hidden;
  font-family: Roboto; }

.nav-container {
  background: transparent;
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  z-index: 70; }
  .nav-container .logo {
    margin-left: 40px;
    color: #F5A623;
    text-transform: uppercase;
    font-family: Niconne;
    font-size: 32px; }

h1, h2, h3, h4, p {
  margin: 0;
  padding: 0; }

h2 {
  color: #4A4A4A;
  font-size: 26px;
  margin-bottom: 16px; }
  h2.tab:before {
    content: '';
    border-left: 4px solid #F5A623;
    margin-left: -14px;
    padding-right: 10px; }

p {
  line-height: 26px;
  color: #6C6C6C;
  font-size: 16px; }

a {
  color: #F5A623;
  border-bottom: 2px solid #F5A623;
  cursor: pointer;
  transition: all .25s ease-in-out;
  text-decoration: none;
  font-weight: bold; }
  a:hover {
    color: #c37d09;
    border-bottom: 2px solid rgba(245, 166, 35, 0); }

.footer {
  position: absolute;
  width: 100vw;
  height: 4px;
  display: block;
  background: #F5A623;
  margin-top: 40px; }

img {
  width: 100%;
  height: auto;
  position: relative;
  z-index: 10;
  display: inline-block; }

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: black;
  display: block;
  opacity: 0;
  transition: all .25s ease-in-out;
  pointer-events: none;
  z-index: 80;
  cursor: pointer; }
  .overlay.is-open {
    opacity: .75;
    pointer-events: auto; }



